import React, { Component } from 'react'
import { Panel, Avatar, Div, Progress, Spinner, FixedLayout, InfoRow } from '@vkontakte/vkui';

const Load =  ({ progress}) => (
    
     <Div>
         <Div align='center' style={{ marginTop: '20%'}}>
             <Avatar size={96} src='https://sun1-95.userapi.com/pigYo8TH8e7MkcdiKecnb8J9xygOvSjefZavPA/UzmrJFHdYVs.jpg'/>
         </Div>
         
                <Div style={{ marginTop: '40%'}}>
                    <Spinner style={{ marginBottom: 12 }}/>
                    <InfoRow header="Добро пожаловать на борт"><Progress value={progress} /></InfoRow>
                    
                </Div>
             
         
     </Div>

)

export default Load;